<template>
  <div class="body1">
    <div class="loginBox">
      <h2>离校申请</h2>





      <form>


        <div class="item">
          <input type="text" required v-model="schoolnum1" id="schoolnum1">
          <label for="">学号：</label>
        </div>

        <div class="item">
          <input type="text" required v-model="name1" id="name1">
          <label for="">姓名：</label>
        </div>
        <button class="btn" @click="login">登录
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </button>

        <button class="btn" @click="register">查询
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </button>



      </form>

    </div>
  </div>
</template>
  


<script>
export default {
  data() {
    return {
      schoolnum1: "",
      name1: ""
    }
  },

  methods: {
    login() {
      var num1 = document.getElementById("schoolnum1").value;
      var num2 = document.getElementById("name1").value;
      // eslint-disable-next-line no-undef
      $.get('http://112.126.72.238:3009/', { schoolnum: num1, name: num2 })
        .done(function (response) {
          console.log(response);

          if (response[2] != 'a') {

            setTimeout(function () {
              window.location.href="?#/StudentData"
            }, 1000); // setTimeout() 的第二个参数是延时时间（单位：毫秒）


            localStorage.setItem("token-num1", num1)
            localStorage.setItem("token-num2", num2)
          } else {
            alert("用户名或密码错误！")
          }


        })


        .fail(function (error) {
          console.log(error);
        });




    },





    register() {
      var num1 = document.getElementById("schoolnum1").value;
      var num2 = document.getElementById("name1").value;
      // eslint-disable-next-line no-undef
      $.get('http://112.126.72.238:3009/', { schoolnum: num1, name: num2 })
        .done(function (response) {
          console.log(response);

          if (response[2] != 'a') {

            
            localStorage.setItem("token-num1", num1)
            localStorage.setItem("token-num2", num2)

            setTimeout(function () {
              window.location.href="?#/StudentDataTable"
            }, 1000); // setTimeout() 的第二个参数是延时时间（单位：毫秒）


          } else {
            alert("用户名或密码错误！")
          }


        })


        .fail(function (error) {
          console.log(error);
        });



      }



  }
}


</script>









<style scoped>
a {
  text-decoration: none;
}

input,
button {
  background: transparent;
  border: 0;
  outline: none;
}

.body1 {
  height: 100vh;
  background-color: #243b55;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #03e9f4;
}

.loginBox {
  background-color: #0c1622;
  margin: 20px;
  border-radius: 10px;
  box-shadow: 0 15px 25px 0 rgba(0, 0, 0, .6);
  padding: 40px 80px;
  box-sizing: border-box;
}

h2 {
  text-align: center;
  color: aliceblue;
  margin-bottom: 30px;
  font-family: 'Courier New', Courier, monospace;
}

.item {
  height: 45px;
  border-bottom: 1px solid #fff;
  margin-bottom: 40px;
  position: relative;
}

.item input {
  width: 100%;
  height: 100%;
  color: #fff;
  padding-top: 20px;
  box-sizing: border-box;
}

.item input:focus+label,
.item input:valid+label {
  top: 0px;
  font-size: 2px;
}

.item label {
  position: absolute;
  left: 0;
  top: 12px;
  transition: all 0.5s linear;
}

.btn {
  padding: 10px 20px;
  margin-top: 30px;
  color: #03e9f4;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.btn:hover {
  border-radius: 5px;
  color: #fff;
  background: #03e9f4;
  box-shadow: 0 0 5px 0 #03e9f4,
    0 0 25px 0 #03e9f4,
    0 0 50px 0 #03e9f4,
    0 0 100px 0 #03e9f4;
  transition: all 1s linear;
}

.btn>span {
  position: absolute;
}

.btn>span:nth-child(1) {
  width: 100%;
  height: 2px;
  background: -webkit-linear-gradient(left, transparent, #03e9f4);
  left: -100%;
  top: 0px;
  animation: line1 1s linear infinite;
}

@keyframes line1 {

  50%,
  100% {
    left: 100%;
  }
}

.btn>span:nth-child(2) {
  width: 2px;
  height: 100%;
  background: -webkit-linear-gradient(top, transparent, #03e9f4);
  right: 0px;
  top: -100%;
  animation: line2 1s 0.25s linear infinite;
}

@keyframes line2 {

  50%,
  100% {
    top: 100%;
  }
}

.btn>span:nth-child(3) {
  width: 100%;
  height: 2px;
  background: -webkit-linear-gradient(left, #03e9f4, transparent);
  left: 100%;
  bottom: 0px;
  animation: line3 1s 0.75s linear infinite;
}

@keyframes line3 {

  50%,
  100% {
    left: -100%;
  }
}

.btn>span:nth-child(4) {
  width: 2px;
  height: 100%;
  background: -webkit-linear-gradient(top, transparent, #03e9f4);
  left: 0px;
  top: 100%;
  animation: line4 1s 1s linear infinite;
}

@keyframes line4 {

  50%,
  100% {
    top: -100%;
  }
}
</style>



