<template>
    <el-form label-width="120px" style="margin:10px">



        <!-- 个人信息 -->
        <el-card class="box-card">
            <template #header>
                <div class="card-header">
                    <span>个人信息：</span>
                </div>
            </template>
            <div class="text item">


                <table>
                    <tr>
                        <td>序号：</td>
                        <td id="qqxuhao"></td>
                    </tr>
                    <tr>
                        <td>学院：</td>
                        <td id="qqxueyuan"></td>
                    </tr>
                    <tr>
                        <td>专业：</td>
                        <td id="qqzhuanye"></td>
                    </tr>
                    <tr>
                        <td>班级：</td>
                        <td id="qqbanji"></td>
                    </tr>
                    <tr>
                        <td>学号：</td>
                        <td id="qqxuehao"></td>
                    </tr>
                    <tr>
                        <td>姓名：</td>
                        <td id="qqxingming"></td>
                    </tr>
                </table>



            </div>
        </el-card>

        <!-- 离校前地址 -->
        <el-card class="box-card" style="margin-top:20px;">
            <template #header>
                <div class="card-header">
                    <span>离校前地址:</span>
                </div>
            </template>

            <div class="text item">



                <el-form-item label="离校:">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="离校日期:" style="width: 100%" v-model="lixiao_riqi"
                            id="lixiao_riqi" />
                    </el-col>
                    <el-col :span="2" class="text-center">
                        <span class="text-gray-500">-</span>
                    </el-col>
                    <el-col :span="11">
                        <el-time-picker placeholder="离校时间:" style="width: 100%" v-model="lixiao_shijian" />
                    </el-col>
                </el-form-item>

                <el-form-item label="宿舍楼号：">
                    <el-select placeholder="请选择宿舍楼号:" v-model="lixiao_sushihaolouhao">
                        <el-option label="1-号公寓" value="1-号公寓" />
                        <el-option label="2-号公寓" value="2-号公寓" />
                        <el-option label="3-号公寓" value="3-号公寓" />
                        <el-option label="4-号公寓" value="4-号公寓" />
                        <el-option label="5-号公寓" value="5-号公寓" />
                        <el-option label="6-号公寓" value="6-号公寓" />
                    </el-select>
                </el-form-item>

                <el-form-item label="宿舍号:">
                    <el-input placeholder="209" v-model="lixiao_sushihao" />
                </el-form-item>

            </div>

        </el-card>


        <!-- 家庭所在地 -->
        <el-card class="box-card" style="margin-top:20px;">
            <template #header>
                <div class="card-header">
                    <span>家庭所在地:</span>
                </div>
            </template>

            <div class="text item">
                <el-form-item label="所在省份:">
                    <el-input placeholder="浙江省" v-model="dizhi_1" />
                </el-form-item>

                <el-form-item label="所在市:">
                    <el-input placeholder="杭州市" v-model="dizhi_2" />
                </el-form-item>


                <el-form-item label="所在区/县:">
                    <el-input placeholder="西湖区" v-model="dizhi_3" />
                </el-form-item>

                <el-form-item label="详细地址:">
                    <el-input type="textarea" placeholder="太阳城x号楼x门xxxx" v-model="dizhi_4" />
                </el-form-item>



            </div>

        </el-card>

        <!-- 交通方式 -->
        <el-card class="box-card" style="margin-top:20px;">


            <template #header>
                <div class="card-header">
                    <span>交通方式:</span>
                </div>
            </template>

            <div class="text item">
                <el-form-item label="三站一场出发:">
                    <el-select placeholder="选自出发地" v-model="jiaotong_chufa">
                        <el-option label="天津滨海机场" value="天津滨海机场" />
                        <el-option label="天津西站" value="天津西站" />
                        <el-option label="天津站" value="天津站" />
                    </el-select>
                </el-form-item>


                <el-form-item label="出发时间:">
                    <el-col :span="11">
                        <el-time-picker placeholder="" style="width: 100%" v-model="jiaotong_shijian" />
                    </el-col>
                </el-form-item>


                <el-form-item label="交通方式:">
                    <el-radio-group v-model="jiaotong_fangshi">
                        <el-radio label="飞机" name="type" />
                        <el-radio label="高铁/火车" name="type" />
                        <el-radio label="公共交通" name="type" />
                        <el-radio label="打车/拼车" name="type" />
                        <el-radio label="自家" name="type" />
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="交通信息照号:">
                    <el-input placeholder="航班，车次，私家车，无" v-model="jiaotong_xinxizhaohao" />
                </el-form-item>

                <el-form-item label="座位号:">
                    <el-input placeholder="可填“无" v-model="jiaotong_zuoweihao" />
                </el-form-item>



            </div>

        </el-card>

        <!-- 终点 -->
        <el-card class="box-card" style="margin-top:20px;">
            <template #header>
                <div class="card-header">
                    <span>终点:</span>
                </div>
            </template>

            <div class="text item">

                <el-form-item label="详细地址:">
                    <el-input type="textarea" placeholder="浙江省杭州市西湖区太阳城x号楼x门xxxx" v-model="zhongdian" />
                </el-form-item>


            </div>

        </el-card>

        <el-button @click="tijiao" type="success" style="margin:30px 20% 30px 20%;width: 60%;">提交</el-button>





    </el-form>
</template>



<script>
import $ from 'jquery';



export default {
    data() {
        return {


            lixiao_riqi: '',
            lixiao_shijian: '',
            lixiao_sushihaolouhao: '',
            lixiao_sushihao: '',

            dizhi_1: '',
            dizhi_2: '',
            dizhi_3: '',
            dizhi_4: '',

            jiaotong_chufa: '',
            jiaotong_shijian: '',
            jiaotong_fangshi: '',
            jiaotong_xinxizhaohao: '',
            jiaotong_zuoweihao: '',

            zhongdian: ''

        }
    },


    mounted() {


        let token1 = localStorage.getItem("token-num1");
        let token2 = localStorage.getItem("token-num2");



        $.ajax({
            url: 'http://112.126.72.238:3009/',
            type: 'GET',
            dataType: 'json',
            data: {
                schoolnum: token1,
                name: token2

            },
            success: function (data) {
                console.log(data);

                var responseElement1 = $('#qqxuhao'); // 选择要显示响应数组元素的目标元素
                responseElement1.text(data[0].序号); // 更新目标元素的文本内容为要显示的值



                var responseElement2 = $('#qqxueyuan'); // 选择要显示响应数组元素的目标元素
                responseElement2.text(data[0].学院); // 更新目标元素的文本内容为要显示的值

                var responseElement3 = $('#qqzhuanye'); // 选择要显示响应数组元素的目标元素
                responseElement3.text(data[0].专业); // 更新目标元素的文本内容为要显示的值





                var responseElement4 = $('#qqbanji'); // 选择要显示响应数组元素的目标元素
                responseElement4.text(data[0].班级); // 更新目标元素的文本内容为要显示的值




                var responseElement5 = $('#qqxuehao'); // 选择要显示响应数组元素的目标元素
                responseElement5.text(data[0].学号); // 更新目标元素的文本内容为要显示的值


                var responseElement6 = $('#qqxingming'); // 选择要显示响应数组元素的目标元素
                responseElement6.text(data[0].姓名); // 更新目标元素的文本内容为要显示的值



            },
            error: function (jqXHR, textStatus, errorThrown) {
                console.error(errorThrown);
            }
        })



    },





    methods: {
        tijiao() {


            $.ajax({
                url: 'http://112.126.72.238:3010/',
                type: 'GET',
                data: {
                    序号: $('#qqxuhao').text(),
                    学院: $('#qqxueyuan').text(),
                    专业: $('#qqzhuanye').text(),
                    班级: $('#qqbanji').text(),
                    学号: $('#qqxuehao').text(),
                    姓名: $('#qqxingming').text(),


                    离校日期: this.lixiao_riqi,
                    离校时间: this.lixiao_shijian,
                    宿舍楼号: this.lixiao_sushihaolouhao,
                    宿舍号: this.lixiao_sushihao,

                    家庭所在省份: this.dizhi_1,
                    家庭所在市: this.dizhi_2,
                    家庭所在区县: this.dizhi_3,
                    家庭详细地址: this.dizhi_4,

                    三站一场出发: this.jiaotong_chufa,
                    出发时间: this.jiaotong_shijian,
                    交通方式: this.jiaotong_fangshi,
                    交通信息照号: this.jiaotong_xinxizhaohao,
                    座位号: this.jiaotong_zuoweihao,

                    终点: this.zhongdian,


                },
                success: function (data) {
                    console.log(data);
                    setTimeout(function () {
                        window.location.href = "?#/StudentDataOk";
                    }, 1000); // setTimeout() 的第二个参数是延时时间（单位：毫秒）

                },
                error: function (jqXHR, textStatus, errorThrown) {
                    console.error(errorThrown);
                }
            })




        }
    }




}
</script>
  
