import { createRouter , createWebHashHistory } from "vue-router";
import MyLogin from "./components/MyLogin"

import StudentData from "./components/StudentData.vue";
import StudentDataOk from "./components/StudentDataOk.vue";
import StudentDataTable from "./components/StudentDataTable.vue"


const router = createRouter({
    history:createWebHashHistory(),
    routes:[
        {
            path:"/",
            component:MyLogin
        },
        {
            path:"/MyLogin",
            component:MyLogin
        },
        {
            path:"/StudentData",
            component:StudentData
        },
        {
            path:"/StudentDataOk",
            component:StudentDataOk
        },
        {
            path:"/StudentDataTable",
            component:StudentDataTable
        }

    ]
});



router.beforeEach((to, from, next) => {
    let token = localStorage.getItem("token-num1");





    if(token || to.path == '/MyLogin' ){
        next();
    }else{
        next("/MyLogin")
    }
})




export default router;